import { useState, useEffect, useCallback } from 'react';
import {
  Card,
  Button,
  Space,
  Typography,
  Alert,
  Spin,
  message,
  Empty,
  Select,
  Divider,
  Tag,
  Switch,
} from 'antd';
import {
  NodeIndexOutlined,
  ReloadOutlined,
  ThunderboltOutlined,
  FilterOutlined,
  ApartmentOutlined,
} from '@ant-design/icons';
import { useApp } from '../contexts/AppContext';
import { ACTION_TYPES } from '../contexts/AppContext';
import apiService from '../services/apiService';
import { MermaidDiagram, GraphStatistics, BottlenecksList } from '../components';
import mermaid from 'mermaid';

const { Title, Text, Paragraph } = Typography;

const DependencyGraphPage = () => {
  const { dispatch } = useApp();
  const [graphData, setGraphData] = useState(null);
  const [loading, setLoading] = useState(false);
  const [layoutDirection, setLayoutDirection] = useState('LR'); // Top-Down, Left-Right, etc.
  const [filteredGraph, setFilteredGraph] = useState(null);
  const [resourceTypeFilter, setResourceTypeFilter] = useState([]);
  const [availableResourceTypes, setAvailableResourceTypes] = useState([]);
  const [useSubgraphs, setUseSubgraphs] = useState(true); // Toggle for subgraph grouping

  // Initialize Mermaid with better settings for complex graphs
  useEffect(() => {
    mermaid.initialize({
      startOnLoad: true,
      theme: 'default',
      flowchart: {
        useMaxWidth: true,
        htmlLabels: true,
        curve: 'basis', // Smoother curves for better readability
        rankSpacing: 80, // More space between ranks
        nodeSpacing: 60, // More space between nodes
      },
      themeVariables: {
        primaryColor: '#007bff',
        primaryTextColor: '#333',
        primaryBorderColor: '#dee2e6',
        lineColor: '#666',
        fontSize: '14px',
      }
    });
  }, []);

  // Extract resource types from graph data
  useEffect(() => {
    if (graphData && graphData.mermaid) {
      const types = extractResourceTypes(graphData.mermaid);
      setAvailableResourceTypes(types);
    }
  }, [graphData]);

  // Extract resource types from Mermaid syntax
  const extractResourceTypes = (mermaidSyntax) => {
    const types = new Set();
    const lines = mermaidSyntax.split('\n');
    
    lines.forEach(line => {
      // Match class definitions like :::vpc, :::subnet, etc.
      const classMatch = line.match(/:::(\w+)/);
      if (classMatch) {
        types.add(classMatch[1]);
      }
    });
    
    return Array.from(types).sort();
  };

  // Parse Mermaid syntax to extract nodes and edges
  const parseMermaidGraph = (mermaidSyntax) => {
    const lines = mermaidSyntax.split('\n');
    const nodes = [];
    const edges = [];
    const classDefs = [];
    let graphDirection = 'TD';

    lines.forEach(line => {
      const trimmedLine = line.trim();

      // Get graph direction
      const directionMatch = trimmedLine.match(/^graph\s+(\w+)/);
      if (directionMatch) {
        graphDirection = directionMatch[1];
        return;
      }

      // Get class definitions
      if (trimmedLine.startsWith('classDef')) {
        classDefs.push(trimmedLine);
        return;
      }

      // Match node definitions: nodeId["label"]:::class
      const nodeMatch = trimmedLine.match(/^(\S+)\["([^"]+)"\](:::(\w+))?/);
      if (nodeMatch) {
        nodes.push({
          id: nodeMatch[1],
          label: nodeMatch[2],
          type: nodeMatch[4] || 'default',
          vpc: extractVpcFromLabel(nodeMatch[2]) // Extract VPC if it's part of the label
        });
        return;
      }

      // Match edges: source --> target
      const edgeMatch = trimmedLine.match(/^(\S+)\s+-->\s+(\S+)/);
      if (edgeMatch) {
        edges.push({
          source: edgeMatch[1],
          target: edgeMatch[2]
        });
      }
    });

    return { nodes, edges, classDefs, graphDirection };
  };

  // Extract VPC ID from node label if present
  const extractVpcFromLabel = (label) => {
    const vpcMatch = label.match(/vpc-[a-f0-9]+/i);
    return vpcMatch ? vpcMatch[0] : null;
  };

  // Group nodes by VPC or resource type
  const groupNodesByVpc = (nodes) => {
    const groups = {};
    const ungrouped = [];

    nodes.forEach(node => {
      // VPC nodes are their own group
      if (node.type === 'vpc') {
        groups[node.id] = {
          name: node.label,
          vpc: node.id,
          type: 'vpc',
          nodes: []
        };
      }
    });

    // Group other nodes by their VPC reference
    nodes.forEach(node => {
      if (node.type === 'vpc') return; // Skip VPC nodes themselves

      let grouped = false;

      // Try to find VPC in node's label or edges
      if (node.vpc) {
        if (groups[node.vpc]) {
          groups[node.vpc].nodes.push(node);
          grouped = true;
        }
      }

      if (!grouped) {
        ungrouped.push(node);
      }
    });

    // Group ungrouped nodes by resource type
    const typeGroups = {};
    ungrouped.forEach(node => {
      if (!typeGroups[node.type]) {
        typeGroups[node.type] = {
          name: `${node.type.toUpperCase()} Resources`,
          type: node.type,
          nodes: []
        };
      }
      typeGroups[node.type].nodes.push(node);
    });

    return { vpcGroups: Object.values(groups), typeGroups: Object.values(typeGroups) };
  };

  // Generate Mermaid syntax with subgraphs
  const generateMermaidWithSubgraphs = (parsedGraph, useSubgraphs = true) => {
    if (!useSubgraphs) {
      // Return original-style syntax without subgraphs
      return reconstructMermaidSyntax(parsedGraph);
    }

    const { nodes, edges, classDefs, graphDirection } = parsedGraph;
    const { vpcGroups, typeGroups } = groupNodesByVpc(nodes);

    let mermaidCode = `graph ${graphDirection}\n\n`;

    // Generate VPC subgraphs
    vpcGroups.forEach((group, index) => {
      if (group.nodes.length === 0) return;

      mermaidCode += `    subgraph vpc_${index}["ðŸŒ VPC: ${group.name}"]\n`;
      mermaidCode += `        direction ${graphDirection === 'LR' ? 'TB' : 'LR'}\n`;
      
      // VPC node itself
      const vpcNode = nodes.find(n => n.id === group.vpc);
      if (vpcNode) {
        mermaidCode += `        ${vpcNode.id}["${vpcNode.label}"]:::${vpcNode.type}\n`;
      }

      // Nodes in this VPC
      group.nodes.forEach(node => {
        mermaidCode += `        ${node.id}["${node.label}"]:::${node.type}\n`;
      });
      
      mermaidCode += `    end\n\n`;
    });

    // Generate resource type subgraphs for ungrouped nodes
    typeGroups.forEach((group, index) => {
      if (group.nodes.length === 0) return;

      const emoji = getResourceTypeEmoji(group.type);
      mermaidCode += `    subgraph type_${index}["${emoji} ${group.name}"]\n`;
      mermaidCode += `        direction ${graphDirection === 'LR' ? 'TB' : 'LR'}\n`;
      
      group.nodes.forEach(node => {
        mermaidCode += `        ${node.id}["${node.label}"]:::${node.type}\n`;
      });
      
      mermaidCode += `    end\n\n`;
    });

    // Add edges
    edges.forEach(edge => {
      mermaidCode += `    ${edge.source} --> ${edge.target}\n`;
    });

    // Add class definitions
    mermaidCode += '\n';
    classDefs.forEach(classDef => {
      mermaidCode += `    ${classDef}\n`;
    });

    return mermaidCode;
  };

  // Get emoji for resource type
  const getResourceTypeEmoji = (type) => {
    const emojiMap = {
      'ec2': 'ðŸ’»',
      'ec2_instance': 'ðŸ’»',
      'instance': 'ðŸ’»',
      'lambda': 'âš¡',
      'rds': 'ðŸ—„ï¸',
      'database': 'ðŸ—„ï¸',
      's3': 'ðŸ“¦',
      'dynamodb': 'ðŸ”·',
      'subnet': 'ðŸ”¸',
      'sg': 'ðŸ”’',
      'security_group': 'ðŸ”’',
      'securitygroup': 'ðŸ”’',
      'lb': 'âš–ï¸',
      'loadbalancer': 'âš–ï¸',
      'load_balancer': 'âš–ï¸',
      'alb': 'âš–ï¸',
      'asg': 'ðŸ“ˆ',
      'auto_scaling_group': 'ðŸ“ˆ',
      'route53': 'ðŸŒ',
      'cloudfront': 'ðŸŒ',
      'ami': 'ðŸ“€',
      'natgw': 'ðŸ”„',
      'nat_gateway': 'ðŸ”„',
      'tg': 'ðŸŽ¯',
      'lt': 'ðŸ“‹',
      'default': 'ðŸ”§'
    };

    return emojiMap[type.toLowerCase()] || emojiMap['default'];
  };

  // Reconstruct Mermaid syntax without subgraphs (original style)
  const reconstructMermaidSyntax = (parsedGraph) => {
    const { nodes, edges, classDefs, graphDirection } = parsedGraph;
    
    let mermaidCode = `graph ${graphDirection}\n`;

    // Add nodes
    nodes.forEach(node => {
      const classStr = node.type !== 'default' ? `:::${node.type}` : '';
      mermaidCode += `    ${node.id}["${node.label}"]${classStr}\n`;
    });

    mermaidCode += '\n';

    // Add edges
    edges.forEach(edge => {
      mermaidCode += `    ${edge.source} --> ${edge.target}\n`;
    });

    // Add class definitions
    mermaidCode += '\n';
    classDefs.forEach(classDef => {
      mermaidCode += `    ${classDef}\n`;
    });

    return mermaidCode;
  };

  // Improve Mermaid syntax for complex graphs
  const improveMermaidSyntax = useCallback((originalSyntax, direction = 'TD', enableSubgraphs = true) => {
    if (!originalSyntax) return originalSyntax;

    // Parse the original Mermaid syntax
    const parsedGraph = parseMermaidGraph(originalSyntax);
    
    // Update graph direction
    parsedGraph.graphDirection = direction;

    // Simplify node labels for better readability
    parsedGraph.nodes = parsedGraph.nodes.map(node => ({
      ...node,
      label: simplifyLabel(node.label)
    }));

    // Generate new syntax with or without subgraphs
    return generateMermaidWithSubgraphs(parsedGraph, enableSubgraphs);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  // Simplify long node labels for better readability
  const simplifyLabel = (label) => {
    // Simplify ARN labels
    if (label.includes('arn:aws:')) {
      // Simplify target group ARNs
      const tgMatch = label.match(/targetgroup\/([^/]+)\//);
      if (tgMatch) {
        return `TG: ${tgMatch[1]}`;
      }
      
      // Simplify load balancer ARNs
      const lbMatch = label.match(/loadbalancer\/(app|net|gwy|nlb|alb)\/([^/]+)\//);
      if (lbMatch) {
        const lbType = lbMatch[1].toUpperCase();
        const lbName = lbMatch[2];
        return `${lbType}: ${lbName}`;
      }
    }
    
    // Shorten instance IDs
    if (label.includes('i-') && label.length > 20) {
      const match = label.match(/i-([a-f0-9]+)/);
      if (match && match[1].length > 8) {
        return label.replace(/i-([a-f0-9]+)/, `i-${match[1].substring(0, 8)}`);
      }
    }
    
    return label;
  };

  // Filter graph by resource types
  const filterGraphByResourceTypes = (mermaidSyntax, types) => {
    if (!types || types.length === 0) return mermaidSyntax;

    const lines = mermaidSyntax.split('\n');
    const filtered = [];
    const nodesToKeep = new Set();

    // First pass: identify nodes to keep based on their class
    lines.forEach(line => {
      types.forEach(type => {
        if (line.includes(`:::${type}`)) {
          // Extract node ID from lines like: nodeId["label"]:::class
          const nodeMatch = line.match(/^\s*(\S+)\[/);
          if (nodeMatch) {
            nodesToKeep.add(nodeMatch[1]);
          }
        }
      });
    });

    // Second pass: keep graph definition, matching nodes, and edges between kept nodes
    lines.forEach(line => {
      // Keep graph declaration
      if (line.match(/^graph\s+\w+/)) {
        filtered.push(line);
        return;
      }
      
      // Keep class definitions
      if (line.match(/^\s*classDef\s+/)) {
        filtered.push(line);
        return;
      }

      // Keep nodes with matching types
      let shouldKeep = false;
      nodesToKeep.forEach(nodeId => {
        if (line.includes(nodeId)) {
          shouldKeep = true;
        }
      });

      if (shouldKeep) {
        filtered.push(line);
      }
    });

    return filtered.join('\n');
  };

  // Apply filters and improvements to graph
  useEffect(() => {
    if (graphData && graphData.mermaid) {
      let processedSyntax = graphData.mermaid;

      // Apply resource type filter
      if (resourceTypeFilter.length > 0) {
        processedSyntax = filterGraphByResourceTypes(processedSyntax, resourceTypeFilter);
      }

      // Apply layout improvements with subgraph option
      processedSyntax = improveMermaidSyntax(processedSyntax, layoutDirection, useSubgraphs);

      setFilteredGraph(processedSyntax);
    } else {
      setFilteredGraph(null);
    }
  }, [graphData, layoutDirection, resourceTypeFilter, useSubgraphs, improveMermaidSyntax]);

  const loadDependencyGraph = async (source = null) => {
    setLoading(true);
    dispatch({
      type: ACTION_TYPES.SET_LOADING,
      payload: { key: 'graph', value: true },
    });

    try {
      const data = await apiService.getDependencyGraph(source);
      setGraphData(data);
      
      dispatch({
        type: ACTION_TYPES.SET_DEPENDENCY_GRAPH,
        payload: data,
      });
      
      const successMessage = source === 'live' 
        ? 'Live discovery graph generated successfully'
        : 'Dependency graph generated successfully';
      
      dispatch({
        type: ACTION_TYPES.SET_STATUS_MESSAGE,
        payload: successMessage,
      });
      
      message.success(successMessage);
    } catch (error) {
      message.error(`Failed to generate graph: ${error.message}`);
      console.error('Failed to load dependency graph:', error);
    } finally {
      setLoading(false);
      dispatch({
        type: ACTION_TYPES.SET_LOADING,
        payload: { key: 'graph', value: false },
      });
    }
  };

  return (
    <div style={{ maxWidth: 1400, margin: '0 auto' }}>
      <div style={{ marginBottom: 24 }}>
        <Title level={2}>
          <NodeIndexOutlined /> Resource Dependency Graph
        </Title>
        <Paragraph type="secondary">
          Visualize dependencies between infrastructure resources and identify potential bottlenecks.
          The graph shows how resources depend on each other, helping you understand the infrastructure topology.
        </Paragraph>
      </div>

      <Card>
        <Space 
          size="large" 
          style={{ 
            width: '100%', 
            display: 'flex', 
            justifyContent: 'space-between',
            alignItems: 'center',
            flexWrap: 'wrap' 
          }}
        >
          <Space wrap>
            <Alert
              message="The dependency graph is rendered using Mermaid diagram syntax for clear visualization"
              type="info"
              showIcon
              style={{ marginBottom: 0 }}
            />
          </Space>
          
          <Space wrap>
            <Button
              type="primary"
              icon={<ReloadOutlined />}
              onClick={() => loadDependencyGraph()}
              loading={loading}
              size="large"
            >
              Generate Graph
            </Button>
            
            <Button
              type="primary"
              icon={<ThunderboltOutlined />}
              onClick={() => loadDependencyGraph('live')}
              loading={loading}
              size="large"
              style={{ 
                background: '#52c41a', 
                borderColor: '#52c41a' 
              }}
            >
              Discovery Live
            </Button>
          </Space>
        </Space>

        {/* Graph Controls - Only show when graph data exists */}
        {graphData && (
          <>
            <Divider />
            <Space 
              size="middle" 
              wrap
              style={{ width: '100%' }}
            >
              <div>
                <Text strong style={{ marginRight: 8 }}>
                  <FilterOutlined /> Graph Layout:
                </Text>
                <Select
                  value={layoutDirection}
                  onChange={setLayoutDirection}
                  style={{ width: 150 }}
                  options={[
                    { label: 'â¬‡ï¸ Top to Bottom', value: 'TD' },
                    { label: 'â¬†ï¸ Bottom to Top', value: 'BT' },
                    { label: 'âž¡ï¸ Left to Right', value: 'LR' },
                    { label: 'â¬…ï¸ Right to Left', value: 'RL' },
                  ]}
                />
              </div>

              <div>
                <Text strong style={{ marginRight: 8 }}>
                  <ApartmentOutlined /> Group by Subgraphs:
                </Text>
                <Switch
                  checked={useSubgraphs}
                  onChange={setUseSubgraphs}
                  checkedChildren="On"
                  unCheckedChildren="Off"
                />
                <Text type="secondary" style={{ marginLeft: 8, fontSize: '12px' }}>
                  {useSubgraphs ? 'Resources grouped by VPC/Type' : 'Flat layout'}
                </Text>
              </div>

              {availableResourceTypes.length > 0 && (
                <div style={{ flex: 1 }}>
                  <Text strong style={{ marginRight: 8 }}>
                    <FilterOutlined /> Filter by Resource Type:
                  </Text>
                  <Select
                    mode="multiple"
                    value={resourceTypeFilter}
                    onChange={setResourceTypeFilter}
                    placeholder="Show all types"
                    style={{ minWidth: 300, maxWidth: '100%' }}
                    allowClear
                    maxTagCount="responsive"
                  >
                    {availableResourceTypes.map(type => (
                      <Select.Option key={type} value={type}>
                        <Tag color="blue">{type}</Tag>
                      </Select.Option>
                    ))}
                  </Select>
                </div>
              )}

              {(resourceTypeFilter.length > 0 || layoutDirection !== 'TD' || !useSubgraphs) && (
                <Button 
                  onClick={() => {
                    setLayoutDirection('TD');
                    setResourceTypeFilter([]);
                    setUseSubgraphs(true);
                  }}
                  size="small"
                >
                  Reset All Filters
                </Button>
              )}
            </Space>
          </>
        )}
      </Card>

      {loading ? (
        <Card style={{ marginTop: 16 }}>
          <div style={{ textAlign: 'center', padding: '50px 0' }}>
            <Spin size="large" />
            <div style={{ marginTop: 16 }}>
              <Text>Analyzing dependencies...</Text>
            </div>
          </div>
        </Card>
      ) : graphData ? (
        <>
          <MermaidDiagram 
            mermaidSyntax={filteredGraph || graphData.mermaid} 
            timestamp={graphData.timestamp}
            nodeMetadata={graphData.graph?.nodes}
          />
          <GraphStatistics metadata={graphData.metadata} />
          <BottlenecksList bottlenecks={graphData.metadata?.bottlenecks} />
        </>
      ) : (
        <Card style={{ marginTop: 16 }}>
          <Empty
            description="No dependency graph generated yet"
            image={Empty.PRESENTED_IMAGE_SIMPLE}
          >
            <Button type="primary" onClick={loadDependencyGraph} icon={<ReloadOutlined />}>
              Generate Graph
            </Button>
          </Empty>
        </Card>
      )}
    </div>
  );
};

export default DependencyGraphPage;